import template from '../assets/template.hbs';
import arr from '../main.json';
console.log(arr);
const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const ul = document.querySelector('.js-menu');
const string = template(arr);
ul.innerHTML = string;
const body = document.querySelector('body');

const switcher = document.querySelector('input.js-switch-input');

body.classList.add(localStorage.getItem('theme'));

if (localStorage.getItem('theme') === Theme.DARK) {
  switcher.checked = true;
}
// const listener = () => {
//   if (body.classList.icludes('dark-theme')) {
//     switcher.setAttribute('checked', 'checked');
//   } else {
//     switcher.setAttribute('checked', ' ');
//   }
// };

// body.addEventListener('DOMContentLoaded', listener);

const switchFunc = () => {
  if (switcher.checked) {
    body.setAttribute('class', Theme.DARK);
    localStorage.setItem('theme', body.classList);
  } else {
    body.setAttribute('class', Theme.LIGHT);
    localStorage.setItem('theme', body.classList);
  }
};

switcher.addEventListener('change', switchFunc);
